import React, { Fragment } from 'react'
// import PropTypes from 'prop-types'
import { connect } from 'dva'
import { routerRedux } from 'dva/router'
import { Row, Col, Select, Input, Table, Button, Modal } from 'antd'
import queryString from 'query-string'

import Model from './WaitingModel'

import style from './waitingSong.less'

const { Option, OptGroup } = Select
const { Search } = Input
const { confirm } = Modal

interface Iprops {
  waitingsong: any,
  loading: any,
  dispatch: any,
  location: any,
}

class WaitingSong extends React.PureComponent<Iprops> {
  
  public openSearch = () => {
    // console.log('gaojishaixuan')
    this.props.dispatch({
      type: 'waitingsong/updateState',
      payload: {
        modalVisible: true,
      },
    })
  }
  public handleSearch = (val: any) => {
    console.log(val, 'sousuo')
  }
  public handleSearchChange = (val: any) => {
    console.log(val, 'select')
  }
  public onSelectChange = (selectedRowKeys: any) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys)
    // this.setState({ selectedRowKeys })
    this.props.dispatch({
      type: 'waitingsong/updateState',
      selectedRowKeys,
    })
  }
  public showConfirm = (data: any) => {
    confirm({
      title: '你确定要发布吗？',
      content: '',
      onOk () {
        console.log('确定')
        console.log(data, 'fabu')
      },
      onCancel () {console.log(2)},
    })
  }
  public render () {
    const {
      waitingsong, dispatch, loading, location,
    } = this.props
    const {
      list, pagination, selectedRowKeys, currentItem, modalVisible,
    } = waitingsong
    const { query, pathname } = location
    const columns = [
      {
        title: '序号',
        dataIndex: 'id',
      },
      {
        title: '图片',
        dataIndex: 'image',
        render: (text: string) => <img alt="Feture" width={26} src={text} />,
      },
      {
        title: '链接',
        dataIndex: 'author',
      },
      {
        title: '上线时间',
        dataIndex: 'date',
        // sorter: true,
        // render: val => <span>{moment(val).format('YYYY-MM-DD HH:mm:ss')}</span>,
      },
      {
        title: '状态',
        dataIndex: 'status',
      },
      {
        title: '操作',
        render: (val: any) => <Button onClick={() => this.showConfirm(val)}>发布</Button>,
      },
    ]
    const listProps = {
      pagination,
      dataSource: list,
      columns,
      rowSelection: {
        selectedRowKeys,
        onChange: (keys: any) => {
          dispatch({
            type: 'waitingsong/updateState',
            payload: {
              selectedRowKeys: keys,
            },
          })
        },
      },
      loading: loading.effects['waitingsong/query'],
      onChange (page: any) {
        dispatch(routerRedux.push({
          pathname,
          search: queryString.stringify({
            ...query,
            page: page.current,
            pageSize: page.pageSize,
          }),
        }))
      },
    }
    const modalProps = {
      item: currentItem,
      visible: modalVisible,
      maskClosable: false,
      // confirmLoading: loading.effects['user/update'],
      title: '高级筛选',
      wrapClassName: 'vertical-center-modal',
      onOk (data: any) {
        console.log(data, 'ok')
      },
      onCancel () {
        dispatch({
          type: 'waitingsong/updateState',
          payload: {
            modalVisible: false,
          },
        })
      },
    }
    return (
      <Fragment>
        <Row className={style.head}>
          <Col span={12}>
            <div>待发布的歌曲共计 <span className={style.textPoint}>3</span> 首</div>
          </Col>
          <Col span={12} className={style.contentRight}>
            <a onClick={this.openSearch}>高级筛选</a>
            <Select
              style={{ width: 200, marginLeft: 8 }}
              showSearch={true}
              placeholder="更多操作"
              onChange={this.handleSearchChange}
            >
              <OptGroup label="发布">
                <Option value="批量发布">批量发布</Option>
                <Option value="查看正在发布的歌曲">查看正在发布的歌曲</Option>
                <Option value="查看发布失败的歌曲">查看发布失败的歌曲</Option>
              </OptGroup>
              <OptGroup label="云发布">
                <Option value="批量云发布">批量发布</Option>
                <Option value="查看正在云发布的歌曲">查看正在云发布的歌曲</Option>
                <Option value="查看云发布失败的歌曲">查看云发布失败的歌曲</Option>
              </OptGroup>
              <OptGroup label="导入">
                <Option value="导入发布歌曲">导入发布歌曲</Option>
                <Option value="导入云发布歌曲">导入云发布歌曲</Option>
                <Option value="导出今天已发布的歌曲">导出今天已发布的歌曲</Option>
              </OptGroup>
            </Select>
            <span className={style.line}>|</span>
            <Search style={{ width: 200 }} placeholder="请输入要搜索的歌曲" onSearch={this.handleSearch} />
          </Col>
        </Row>
        <Table
          {...listProps}
          bordered={true}
          // scroll={{ x: 1200 }}
          // columns={columns}
          // simple
          rowKey={(record: any) => record.id}
        />
        {modalVisible && <Model {...modalProps} />}
      </Fragment>
    )
  }
}

export default connect(({ waitingsong, loading, dispatch }: Iprops) => ({ waitingsong, loading, dispatch }))(WaitingSong)



