import React from 'react'
// import PropTypes from 'prop-types'
import { Form, Input, Modal, DatePicker, Select } from 'antd'

const FormItem = Form.Item
const { RangePicker } = DatePicker
const { Option } = Select

const formItemLayout = {
  labelCol: {
    span: 6,
  },
  wrapperCol: {
    span: 14,
  },
}
interface Iprops {
  item: any,
  onOk: any,
  form: any,
  [propsname: string]: any
}

const modal = ({
  item = {},
  onOk,
  form: {
    getFieldDecorator,
    validateFields,
    getFieldsValue,
  },
  ...modalProps
}: Iprops) => {
  const handleOk = () => {
    validateFields((errors:any) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
        key: item.key,
      }
      onOk(data)
    })
  }

  const modalOpts = {
    ...modalProps,
    onOk: handleOk,
  }
  const handleChange = (info: any) => {
    console.log(info, 'info')
  }
  const onChange = (val: any) => {
    console.log(val)
  }
  // console.log(modalOpts)
  return (
    <Modal {...modalOpts}>
      <Form layout="horizontal">
        <FormItem label="语种" hasFeedback={true} {...formItemLayout}>
          {getFieldDecorator('id', {
            rules: [
              {
                // required: true,
              },
            ],
          })(<Select
            showSearch={true}
            placeholder="请选择语种"
            optionFilterProp="children"
            onChange={handleChange}
            filterOption={(input: string, option: any) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            <Option value="粤语">粤语</Option>
            <Option value="国语">国语</Option>
          </Select>)}
        </FormItem>
        <FormItem label="视频版本" hasFeedback={true} {...formItemLayout}>
          {getFieldDecorator('video', {
            rules: [
              {
                // required: true,
              },
            ],
          })(<Select
            showSearch={true}
            placeholder="请选择视频版本"
            optionFilterProp="children"
            onChange={handleChange}
            filterOption={(input: string, option: any) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            <Option value="演唱会">演唱会</Option>
            <Option value="KTV">KTV</Option>
          </Select>)}
        </FormItem>
        <FormItem label="曲种" hasFeedback={true} {...formItemLayout}>
          {getFieldDecorator('singtype', {
            rules: [
              {
                // required: true,
              },
            ],
          })(<Select
            showSearch={true}
            placeholder="请选择曲种"
            optionFilterProp="children"
            onChange={handleChange}
            filterOption={(input: string, option: any) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            <Option value="其它">其它</Option>
            <Option value="新歌">新歌</Option>
          </Select>)}
        </FormItem>
        <FormItem label="歌星" hasFeedback={true} {...formItemLayout}>
          {getFieldDecorator('name', {
            initialValue: item.name,
            rules: [
              {
                // required: true,
              },
            ],
          })(<Input placeholder="请输入歌星名字" />)}
        </FormItem>
        <FormItem label="日期" {...formItemLayout}>
          {getFieldDecorator('date', {
            defaultValue: item.date,
            rules: [
              {
                // required: true,
              },
            ],
          })(<RangePicker onChange={onChange} />)}
        </FormItem>
        <FormItem label="关键字" hasFeedback={true} {...formItemLayout}>
          {getFieldDecorator('keyname', {
            initialValue: item.keyname,
            rules: [
              {
                // required: true,
              },
            ],
          })(<Input placeholder="请输入关键字" />)}
        </FormItem>
      </Form>
    </Modal>
  )
}

export default Form.create()(modal)



